


.OTP {
	padding-bottom: 5px;
}

.signupbox {
	margin-top: -50px;
	width: 700px;
	height: 100%;
	box-shadow: 0px 0px 5px;
}

.leftbox {
	background: #FF4949;
	height: 100vh;
	padding: 10px;
	justify-content: center;
}

.info {
	text-align:center; 
	color: white; 
}
.infohead {
	   font-weight: bold;
	font-size : 1.5rem;
}
.infobody {

}

.rightbox {
	padding: 20px;
	margin-top: 5px;
	
}

.SignUpCont {
	width: 100%;
}
.signupheader {
	font-weight: bold;
	font-size : 2rem;
	text-align:center;
	padding-bottom: 20px;
}

.Mobile {
	margin-bottom: 10px;
}
.footer {
	font-size: .8rem;
	position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
 
  color: black;
  text-align: center;

}


input[type='number'] {
    -moz-appearance:textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

.signupform {
	padding: 20px;
}

.Form-header {
	font-weight: bold;
	font-size : 1.5rem;
	text-align: center;
	padding-bottom: 20px;
}
.Name {
	padding-bottom: 10px;

}
.Pass {
	padding-bottom: 10px;
	padding-top: 10px;
	
}
.Email {
	padding-bottom: 20px;
	
}


@media screen and (max-width: 1200px) {

	.infohead {
		color: #FF4949;
	}
	.infobody {
		color: black;
	}

	.close {
		position: absolute;
	}

	.signupbox {
	width: 100%;
	height: 100%;
	box-shadow: 0px 0px 0px;
	
}

	.leftbox {
	background: #FFFF;
	height: 100px;
	padding: 0px;
	margin: 0px;
	position: relative;
	top: 0;
	
	justify-content: center;
}
.rightbox {
	padding: 0px;
	
}
.dialog {
	width: 100%;
	margin: 0px;
	padding: 0px;

}

.signupheader {
	font-weight: normal;
	font-size : 1.5rem;
}

	}