.sidebar {
	background:#F5F5F5;
}

.dashboardbody {
	border-top:solid 1px #F5F5F5;
}
.uploadedItem {
	width: 100%;
	height: auto;
}
.uploadedItemBody {
	width: 100%;
	height: 300px;
	padding: 15px;
}
.uploadedItemRegNo {
	padding: 1px;
}
.registration-ui {
	background-color: white;
	padding: .25em .25em .25em 1em;
	font-weight: bold;
	font-size: 1.1em;
	border-radius: 5px;
	border: 1px solid #000;
	box-shadow: 1px 1px 1px #ddd;
	position: relative;
	color: 	black;
  font-family: helvetica, ariel, sans-serif;
}

.registration-ui:before {
	content: 'IND';
	display: block;
	width: 30px;
	height: 100%;
	position: absolute;
	top: 0;
	border-radius: 5px 0 0 5px;
	color : #000080;
	font-size: .4em;
	line-height: 30px;
	padding-left: 5px;
}
.delInfoMob {
	display: flex;
	flex-direction: row;
}



.uploadedItemName {
	font-weight: bolder;
	font-size: 1.5rem;
	margin-right: 10px;
}
.uploadedItemBrand {
	color: #585656;
}
.uploadedItemPrice {
	font-size: 1.2rem;
}
.uploadedItemInfo {
	display: flex;
	flex-direction: row;

}

.uploadedItemAddress {
	font-size: 1rem;
}
.uploadedItemCity {
	margin-right: 5px;
}


.tmlineBox {
	min-width: 320px;
	height: auto;
	
	
	
}
.secondaryColor {
	background-color: #FF4949;
}
.colorGreen {
	color: green;
}
.txtBox {
	padding: 5px;
}
.delName {
	font-weight: bold;
}
.delMob {
	font-weight: bold;
}
.delMobTxt {
	color: green;
}
.delStart {
	font-weight: bold;


}
.delStartTxt {
	color: green;

}
.delEnd {
	font-weight: bold;
}
.delEndTxt {
	color: green;

}
.expandTxt {
	font-size: .7rem;
	font-style: italic;
	color: grey;
}
@media screen and (max-width: 600px) {
.txtBox {
	font-size: .8rem;

}
