.close {
	margin : 20px;
	
}

.container {

	width: 350px;
	height: 400px;

}

.LoginTitle {
	margin:25px;
	font-weight: bold;
	font-family: 'Bebas Neue', cursive;
	font-size: 2rem;
	padding-bottom: 10px;
	border-bottom: solid 1px grey;
}

.EOP {
	margin-bottom: 15px;
}
.PASS {
	margin-bottom: 15px;
}

.btn-login {
	border: 1px solid #FF4949;
   background: #FF4949;
   width: 100%;

   -webkit-border-radius: 7px;
   -moz-border-radius: 7px;
   border-radius: 7px;
   color: white;
   font-size: 1.1rem;
   height: 40px;
   text-decoration: none;
   vertical-align: middle;
   justify-content: center;
}
.btn-login:hover {
	flex :1;
	background: #FE3232;
	color: white;


}


