
.disabled {
    pointer-events: none;
    opacity: 0.4;
}

.Jumbo {
	background:#FF4949;
	height: 200px;
	width: 100%;
	display: flex;
	overflow: auto;
	padding: 0px;


}

 .SearchBox {
   position: relative;
   justify-content: center;
   width: 100%;
   height: 20%;
   top:40%;
   border: solid 1px #FF4949;
 

}



#searchlogo{
    

	position: absolute;
	width: 39px;
	height: 39px;
	background: white;
	border-right:solid 0px white;
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
	float: right;
	padding: 5px;


}
.search {
	width: 38%;
	height: 39px;
	font-size: 22px;
	border:solid 0px white;
	font-family: 'Poppins', sans-serif;
	padding: 18px;
	font-size: 1.08rem;
	border-radius:5px 0px 0px 5px;
	overflow-y: hidden;
}

.search:focus {
	outline: none;
	font-family: 'Poppins', sans-serif;
}

